export default "/* .container{\n    padding-top:100px;\n}\n\n.imageResize{\n\n    width: 100px !important; \n    height: 100px !important;\n}\n\n.space{\n    margin-bottom: 30px; */\n\n\n/* } */\n\n\n/* .AddedMessage{\n    padding: 20px;\n    background-color:#249424;\n    color: white;\n    margin-bottom: 15px;\n    position: fixed;\n    text-align: center;\n    top: 0;\n    width: 100%;\n    margin: 0 auto;  \n    z-index:9999; \n    \n    }\n\n    .RemovedMessage{\n        padding: 20px;\n        background-color:red;\n        color: white;\n        margin-bottom: 15px;\n        position: fixed;\n        text-align: center;\n        top: 0;\n        width: 100%;\n        margin: 0 auto;  \n        z-index:9999; \n        \n        } */\n\n\n/* -------Single Product Details--------- */\n\n\n.small-container {\n    max-width: 1080px;\n    margin: auto;\n    padding-left: 25px;\n}\n\n\n.row {\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-align: center;\n            align-items: center;\n    flex-wrap: wrap;\n    justify-content: space-around;\n}\n\n\n.col-2 {\n    flex-basis: 50%;\n    min-width: 300px;\n}\n\n\n.col-2 p {\n    margin-bottom: 50px;\n    color: #555;\n}\n\n\n.col-2 p a {\n    text-decoration: none;\n    color: #555;\n}\n\n\n.col-2 p a:visited {\n    text-decoration: none;\n}\n\n\n.single-product {\n    margin-top: 80px;\n}\n\n\n.single-product .col-2 img {\n    padding: 0;\n}\n\n\n.single-product .col-2 {\n    padding: 20px;\n}\n\n\n.single-product h4 {\n    margin: 20px 0;\n    font-size: 22px;\n    font-weight: bold;\n}\n\n\n.single-product select {\n    display: block;\n    padding: 10px;\n    margin-top: 20px;\n}\n\n\n.single-product input {\n    width: 50px;\n    height: 40px;\n    padding-left: 10px;\n    font-size: 20px;\n    margin-right: 10px;\n    border: 1px solid #ff523b;\n}\n\n\ninput:focus {\n    outline: none;\n}\n\n\n.single-product .fa {\n    color: #ff523b;\n    margin-left: 10px;\n}\n\n\n.small-img-row {\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-pack: justify;\n            justify-content: space-between;\n}\n\n\n.small-img-col {\n    flex-basis: 24%;\n    cursor: pointer;\n}\n\n\n.col-2 img {\n    max-width: 100%;\n    padding: 50px 0;\n}\n\n\n.col-2 h1 {\n    font-size: 50px;\n    line-height: 60px;\n    margin: 25px 0;\n}\n\n\n.btn {\n    display: inline-block;\n    background: #ff523b;\n    color: #fff;\n    padding: 8px 30px;\n    margin: 30px 0;\n    border-radius: 30px;\n    -webkit-transition: background 0.5s;\n    transition: background 0.5s;\n}\n\n\n.btn:hover {\n    background: #563434;\n    cursor: pointer;\n}\n\n\nh4 {\n    color: #282c3f;\n    font-weight: normal;\n    /* overflow: hidden; */\n    font-size: 14px;\n    font-weight: 400;\n}\n\n\n@media screen and (max-width:800px) {\n    .small-container {\n        /* margin: auto !important; */\n        padding-left: 10px;\n        padding-right: 10px;\n    }\n    .col-2 img {\n        max-width: 100%;\n        /* padding: 50px 0; */\n    }\n    .row {\n        text-align: center;\n    }\n    .col-2,\n    .col-3,\n    .col-4 {\n        flex-basis: 100%;\n    }\n    .single-product .row {\n        text-align: left;\n    }\n    .single-product .col-2 {\n        padding: 20px 0;\n    }\n    .single-product h1 {\n        font-size: 26px;\n        line-height: 32px;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"