export default "a {\n    cursor: pointer;\n}\n\n.cart-page {\n    margin: 80px auto;\n}\n\ntable {\n    width: 100%;\n    border-collapse: collapse;\n    /* table-layout: fixed; */\n}\n\n.cart-info {\n    display: -webkit-box;\n    display: flex;\n    /* align-items: center; */\n    flex-wrap: wrap;\n    /* justify-content: space-around; */\n    width: 500px !important;\n}\n\nth {\n    text-align: left;\n    padding: 5px;\n    color: #fff;\n    background: #ff523b;\n    font-weight: normal;\n}\n\n.product {\n    width: 300px !important;\n}\n\ntd {\n    padding: 10px 5px;\n    width: 100px;\n    max-width: 100px;\n    white-space: pre-wrap;\n    /* css-3 */\n    white-space: -moz-pre-wrap;\n    /* Mozilla, since 1999 */\n    white-space: -pre-wrap;\n    /* Opera 4-6 */\n    white-space: -o-pre-wrap;\n    /* Opera 7 */\n    word-wrap: break-word;\n    /* Internet Explorer 5.5+ */\n}\n\ntd .bigger {\n    width: 500px !important;\n}\n\na.btn {\n    color: #ff523b;\n    font-size: 12px;\n}\n\ntd img {\n    width: 80px;\n    height: 80px;\n    margin-right: 10px;\n}\n\n.total-price {\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-pack: end;\n            justify-content: flex-end;\n}\n\n.total-price table {\n    border-top: 3px solid #ff523b;\n    width: 100%;\n    max-width: 400px;\n}\n\ntd:last-child {\n    text-align: right;\n}\n\nth:last-child {\n    text-align: right;\n}\n\n.small-container {\n    max-width: 1080px;\n    margin: auto;\n    padding-left: 25px;\n    margin-top: 70px;\n}\n\n.small-container .empty-cart {\n    text-align: center;\n}\n\n.empty-cart .btn {\n    display: inline-block;\n    background: #ff523b;\n    color: #fff;\n    padding: 8px 30px;\n    margin: 30px 0;\n    border-radius: 30px;\n    -webkit-transition: background 0.5s;\n    transition: background 0.5s;\n    text-decoration: none;\n}\n\n.empty-cart .btn:hover {\n    background: #563434;\n}\n\ntable .qtyArea {\n    display: inline-block;\n    /* table-layout: fixed; */\n    /* width: auto; */\n    /* float: right; */\n}\n\n.qtyArea th {\n    border: 1px solid black;\n    line-height: 1;\n    text-align: center;\n}\n\n.qtyArea .qtyNumber {\n    width: 60%;\n    -webkit-box-pack: center;\n            justify-content: center;\n}\n\n.qtyArea a {\n    cursor: pointer;\n    color: white;\n    font-weight: bold;\n}\n\n.checkoutBtn {\n    float: right;\n    /* display: inline-block; */\n    background: #ff523b;\n    color: #fff;\n    padding: 8px 30px;\n    margin: 30px 0;\n    border-radius: 30px;\n    -webkit-transition: background 0.5s;\n    transition: background 0.5s;\n    text-decoration: none;\n    cursor: pointer;\n}\n\n.checkoutBtn:hover {\n    background: #563434;\n}\n\n@media screen and (max-width:600px) {\n    .small-container {\n        padding-left: 25px;\n        padding-right: 25px;\n    }\n    .cart-info p {\n        display: none\n    }\n    table .qtyArea {\n        margin: auto;\n    }\n    .cart-info {\n        width: 100px !important;\n    }\n    .checkoutBtn {\n        /* width: 100px !important;\n        height: 100px !important; */\n        float: none;\n        margin-left: 75px;\n        margin-right: 75px;\n        text-align: center;\n        /* margin-left: 150px; */\n        display: inline-block\n    }\n}\n\n@media screen and (max-width:1024px) {\n    table .qtyArea {\n        margin: auto;\n    }\n    .small-container {\n        padding-left: 25px;\n        padding-right: 25px;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"